namespace $.$$ {
	export class $mol_resizer_ver extends $.$mol_resizer_ver {


		divider(id?: any) {
			return this.Divider(id);
		}

		@$mol_mem
		pages_wrapped() {
			let extended: any = [];
			
			for (let i = 0; i < this.pages().length; i++) {
				extended.push(this.pages()[i])
				if (i < this.pages().length - 1)
					extended.push(
						this.divider(i + 1)
					)
		}
			return extended as readonly []
}

	}
}
