namespace $ { export class $mol_splitter_hor extends $mol_grip {

	/**
	 *  ```
	 *  type \horizontal
	 *  ```
	 **/
	type() {
		return "horizontal"
	}

	/**
	 *  ```
	 *  title \
	 *  ```
	 **/
	title() {
		return ""
	}

	/**
	 *  ```
	 *  Sub <= Divider
	 *  ```
	 **/
	Sub() {
		return this.Divider()
	}

	/**
	 *  ```
	 *  Divider $mol_view sub / <= title
	 *  ```
	 **/
	@ $mol_mem
	Divider() {
		return (( obj )=>{
			obj.sub = () => [ this.title() ] as readonly any[]
			return obj
		})( new this.$.$mol_view(  ) )
	}

} }

