namespace $ { export class $mol_splitter_ver extends $mol_grip {

	/**
	 *  ```
	 *  type \vertical
	 *  ```
	 **/
	type() {
		return "vertical"
	}

	/**
	 *  ```
	 *  Sub <= Divider
	 *  ```
	 **/
	Sub() {
		return this.Divider()
	}

	/**
	 *  ```
	 *  Divider $mol_view sub / \
	 *  ```
	 **/
	@ $mol_mem
	Divider() {
		return (( obj )=>{
			obj.sub = () => [ "" ] as readonly any[]
			return obj
		})( new this.$.$mol_view(  ) )
	}

} }

