namespace $.$$ {
	export class $mol_splitter_ver extends $.$mol_splitter_ver {

		set_style_for_sibling(node: string, val: string) {
			(this.dom_node()[`${node}ElementSibling`] as HTMLElement).style['height'] = val;
		}

		pos_y(val?: number) {
			this.setPos(val, 'vertical')
		}

		clamp(num: number, min: number, max: number) {
			return num < min ? min : num > max ? max : num;
		}

		node() {
			return (this.dom_node().parentNode as HTMLElement);
		}

		min_height_node(sibling: string) {
			const tmp = (this.dom_node()[`${sibling}ElementSibling`] as HTMLElement).style.height;
			(this.dom_node()[`${sibling}ElementSibling`] as HTMLElement).style.height = 'auto'
			const min_height = (this.dom_node()[`${sibling}ElementSibling`] as HTMLElement).clientHeight;
			(this.dom_node()[`${sibling}ElementSibling`] as HTMLElement).style.height = tmp;

			return min_height;
		}
		setPos(pos: number, type: string) {
			const main = (this.dom_node().parentNode as HTMLFormElement).clientHeight;
			const { top, left } = (this.dom_node()[`previousElementSibling`] as HTMLElement).getBoundingClientRect();
			const { bottom, height } = (this.dom_node()[`nextElementSibling`] as HTMLElement).getBoundingClientRect()
			const min_height_top = this.min_height_node('previous')
			const min_height_bottom = this.min_height_node('next');

			let rect_size = bottom - top;
			const percentage = (rect_size / main) * 100;
			const px = pos - top;


			if (bottom - px < 0 || bottom - px < min_height_bottom) return
			if (px - top < 0 || px - top < min_height_top) return

			const calculated1 = this.clamp(px - top, min_height_top, rect_size) + 3
			this.set_style_for_sibling('previous', `${(calculated1 / rect_size) * percentage}%`);

			const calculated2 = this.clamp(rect_size - (px - top), min_height_bottom, rect_size) + 3
			this.set_style_for_sibling('next', `${(calculated2 / rect_size) * percentage}%`);
		}

		sub() {
			(this.dom_node() as HTMLElement).style['flex-direction'] = this.type() == 'horizontal' ? 'row' : 'column'
			return [this.Divider()] as readonly any[]
		}



	}
}
