namespace $.$$ {
	export class $ts_resizer_hor extends $.$ts_resizer_hor {


		divider(id?: any, title?: any) {
			return this.Divider(id, title);
		}

		@$mol_mem_key
		Divider(id: any, title?: any) {
			return ((obj) => {
				obj.title = () => title
				return obj
			})(new this.$.$ts_splitter_hor())
		}

		@$mol_mem
		pages_wrapped() {
			let extended: any = [];

			for (let i = 0; i < this.pages().length; i++) {
				extended.push(this.pages()[i])
				if (i < this.pages().length - 1)
					extended.push(
						this.divider(i + 1, this.pages()[i]?.title())
					)
			}
			return extended as readonly []
		}

	}
}
