namespace $.$$ {
	export class $ts_select extends $.$ts_select {


		items_wrapped() {
			let items: any[] = [];
			this.items().map(c => {
				items.push(this.menuitem(c))
			})
			return items
		}

		@$mol_mem
		select(value: any) {
			return this.selected(value) || null
		}


		@$mol_mem_key
		menuitem(lang: any) {
			return ((obj) => {
				obj.sub = () => [lang]
				obj.event = () => ({
					"click": (event?: any) => this.select(lang),
				})
				return obj
			})(new this.$.$ts_numl())
		}

	}
}
